function servicesTabs() {
    const servicesItem = document.querySelectorAll('.services__item');
    const servicesInfo = document.querySelectorAll('.services__info-item');

    $('.services__item').on('click', function () {
        servicesItem.forEach((item, i) => {
            item.classList.remove('active');

            if (this == item) {
                hideServicesInfo(i);
                item.classList.add('active');
                servicesInfo[i].classList.add('active');


                toggleNumbers(i);
            }
        });
    });

    function hideServicesInfo(number) {
        servicesInfo.forEach((item, i) => {
            if (i != number) {
                item.classList.remove('active');
            } else {
                item.classList.add('active');
            }
        });
    }
}

function initNumbersList() {
    const servicesItem = document.querySelectorAll('.services__item');

    servicesItem.forEach((item, i) => {
        let div = document.createElement('div');
        div.innerText = 1 + i;
        $('.services__numbers-list').append(div);
    });
}

function toggleNumbers(num) {
    let translateY = $('.services__numbers-wrapper').outerHeight();
    $('.services__numbers-list').css({
        transform: `translateY(-${translateY * num}px)`
    });

    $('.services__big-num').text("0" + (1 + num));
}

servicesTabs();
initNumbersList();