setTimeout(() => {
    $('.cases__list').masonry({
        itemSelector: '.cases__item',
        gutter: 20
    });
}, 200);

function showMoreCases() {
    $(".cases__btn").click(function () {
        $(this).closest(".cases__list").find('.cases__item').css("display", "block");
        $(this).closest(".cases__item--btn").css("display", "none");
        
        $('.cases__list').masonry({
            itemSelector: '.cases__item',
            gutter: 20
        });
    });
}

showMoreCases();