function toggleMenu() {
    const navItem = document.querySelector('.nav__item--subnav');

    // let activeMenu;

    navItem.addEventListener('click', (e) => {
        const target = e.target;

        if (window.innerWidth >= 1024 && target.classList.contains("nav__item--subnav")) {
            if (navItem.classList.contains('active')) {
                navItem.classList.remove('active');
                $('.subnav').slideUp(220);
                showScroll();
            } else {
                navItem.classList.add('active');
                $('.subnav').slideDown(220);
                hideScroll();
            }
        }
    });
}

function headerScroll() {
    window.addEventListener('scroll', function () {
        if (pageYOffset >= 1) {
            $('.header').addClass('active');
        } else {
            $('.header').removeClass('active');
        }
    }, { passive: true });
}

function toggleSubnav() {
    $('.nav__item--subnav').on('click', function () {
        if (window.innerWidth <= 1023) {
            $(this).toggleClass('active');
            $('.subnav').slideToggle(300);
        }
    });
}

function toggleMobMenu() {
    $('.burger').on('click', function () {
        if ($(this).hasClass('active')) {
            $(this).removeClass('active');
            $('.nav').slideToggle(300);
            $('.nav').removeClass('active');
            showScroll();
        } else {
            $(this).addClass('active');
            $('.nav').slideToggle(300);
            $('.nav').addClass('active');
            $('.header__inner').toggleClass('active');
            hideScroll();
        }
    });
}

toggleMenu();
headerScroll();
toggleSubnav();
toggleMobMenu();
