
let mySwiper = undefined;
let screenWidth;

const verticleSlider = document.querySelector(".verticle-slider");

function initSwiper() {
    screenWidth = $(window).width();
    if (screenWidth > 1023 && mySwiper == undefined && verticleSlider) {
        mySwiper = new Swiper(verticleSlider, {
            allowTouchMove: false,
            speed: 700,
            preventInteractionOnTransition: true,
            direction: "vertical",
            effect: "creative",
            creativeEffect: {
                prev: {
                    shadow: false,
                    translate: [0, "-100%", 0],
                },
                next: {
                    translate: [0, "100%", 0],
                },
            },
            pagination: {
                el: ".swiper-pagination",
                clickable: true,
            },
        });

        function sliderCounter() {
            const sliderPagination = document.querySelector('.verticle-slider__pagination2');
            const sliderCount = document.querySelector('.verticle-slider__count');

            if (sliderPagination) {
                const sliderBullet = sliderPagination.childNodes;

                sliderBullet.forEach((item, i) => {
                    item.innerText = i + 1;
                });

                sliderCount.innerText = "/" + sliderBullet.length;
            }
        }

        sliderCounter();

        mySwiper.on('slideChange', function () {
            if (mySwiper.activeIndex > 0) {
                $('.header').addClass('active');
            } else {
                $('.header').removeClass('active');
            }
        });

        function sliderWheelControl() {
            const slider = document.querySelector('.verticle-slider');

            slider.addEventListener('wheel', (e) => {

                let deltaY = e.deltaY;

                if (screenWidth > 1023) {
                    let activeSlide = document.querySelector('.swiper-slide-active');

                    if (Math.round(activeSlide.scrollHeight) - Math.round(activeSlide.scrollTop) == Math.round(activeSlide.clientHeight) && deltaY > 0) {
                        mySwiper.slideNext();
                    } else if (activeSlide.scrollTop == 0 && deltaY < 0) {
                        mySwiper.slidePrev();
                    }
                }
            });
        }

        sliderWheelControl();

    } else if (screenWidth < 1024 && mySwiper != undefined && verticleSlider) {
        mySwiper.destroy();
        mySwiper = undefined;
    }
}

initSwiper();



var optimizedResize = (function () {
    var callbacks = [],
        running = false;

    // fired on resize event
    function resize() {
        if (!running) {
            running = true;

            if (window.requestAnimationFrame) {
                window.requestAnimationFrame(runCallbacks);
            } else {
                setTimeout(runCallbacks, 66);
            }
        }
    }

    // run the actual callbacks
    function runCallbacks() {
        callbacks.forEach(function (callback) {
            callback();
        });

        running = false;
    }

    // adds callback to loop
    function addCallback(callback) {
        if (callback) {
            callbacks.push(callback);
        }
    }

    return {
        // public method to add additional callback
        add: function (callback) {
            if (!callbacks.length) {
                window.addEventListener("resize", resize);
            }
            addCallback(callback);
        },
    };
})();

optimizedResize.add(function () {
    initSwiper();
});



